.todo {
    background-color: #C780FA;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.template {
    background-color: #E3ACF9;
    margin: auto;
    height: 75%;
    padding: 10px;
    width: 30%;
    -webkit-box-shadow: 0px 0px 14px 4px rgba(0,0,0,0.35); 
    box-shadow: 0px 0px 14px 4px rgba(0,0,0,0.35);
}

.header {
    text-align:center;
}

.screen {
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.text {
    border: none;
    background-color: #E3ACF9;
    border-bottom: 1px solid black;
    width: 50%;
    margin:0px 20px;
}
.text:focus{
    outline: none;
}

.add {
    width:50px;
    border-radius: 5px;
    border: 1px solid black;
    background-color: #E3ACF9 ;
    color:black;
    font-weight: bold;
    font-size:medium;
}

.list {
    margin: 20px;
    height: 75%;
    padding-right: 10px;
    overflow-y: scroll;
}
.list::-webkit-scrollbar { 
    display: none;  /* Safari and Chrome */
}
  
.listItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    border: 1px solid black;
    border-radius: 10px;
    padding: 10px;
}

.notDone {
    text-decoration: line-through;
    opacity: 0.6;
}